import React, { useState, useEffect } from 'react';
import { educationAPI, subjectAPI, uploadAPI } from '../services/api';
import { FiPlus, FiEdit, FiTrash2, FiX, FiUpload, FiSettings, FiImage } from 'react-icons/fi';
import './Common.css';

function Education() {
    const [records, setRecords] = useState([]);
    const [subjects, setSubjects] = useState([]);
    const [loading, setLoading] = useState(true);
    const [showModal, setShowModal] = useState(false);
    const [showSubjectModal, setShowSubjectModal] = useState(false);
    const [currentRecord, setCurrentRecord] = useState(null);
    const [selectedMonth, setSelectedMonth] = useState(new Date().toISOString().slice(0, 7));
    const [uploadedFile, setUploadedFile] = useState(null);
    const [uploading, setUploading] = useState(false);
    const [newSubject, setNewSubject] = useState({ name: '', description: '' });
    const [formData, setFormData] = useState({
        yearMonth: new Date().toISOString().slice(0, 7),
        weekNumber: 1,
        subject: '',
        content: '',
        imagePath: ''
    });

    useEffect(() => {
        fetchSubjects();
        fetchRecords();
    }, [selectedMonth]);

    const fetchSubjects = async () => {
        try {
            const response = await subjectAPI.getAll();
            setSubjects(response.data);
        } catch (error) {
            console.error('Error fetching subjects:', error);
        }
    };

    const fetchRecords = async () => {
        try {
            const response = await educationAPI.getByYearMonth(selectedMonth);
            setRecords(response.data);
        } catch (error) {
            console.error('Error fetching education records:', error);
        } finally {
            setLoading(false);
        }
    };

    const handleFileUpload = async (e) => {
        const file = e.target.files[0];
        if (!file) return;

        //   
        if (!file.type.startsWith('image/')) {
            alert('   .');
            return;
        }

        setUploading(true);
        try {
            const response = await uploadAPI.uploadImage(file);
            setUploadedFile(response.data);
            setFormData(prev => ({ ...prev, imagePath: response.data.path }));
            alert(' .');
        } catch (error) {
            console.error('Error uploading file:', error);
            alert('    .');
        } finally {
            setUploading(false);
        }
    };

    const handleAddSubject = async () => {
        if (!newSubject.name.trim()) {
            alert(' .');
            return;
        }
        try {
            await subjectAPI.create(newSubject);
            fetchSubjects();
            setShowSubjectModal(false);
            setNewSubject({ name: '', description: '' });
            alert(' .');
        } catch (error) {
            console.error('Error adding subject:', error);
            alert('    .');
        }
    };

    const handleDeleteSubject = async (id) => {
        if (!window.confirm('  ?')) return;
        try {
            await subjectAPI.delete(id);
            fetchSubjects();
        } catch (error) {
            console.error('Error deleting subject:', error);
            alert('    .');
        }
    };

    const openModal = (record = null) => {
        if (record) {
            setCurrentRecord(record);
            setFormData({
                yearMonth: record.yearMonth,
                weekNumber: record.weekNumber,
                subject: record.subject,
                content: record.content || '',
                imagePath: record.imagePath || ''
            });
            setUploadedFile(record.imagePath ? { path: record.imagePath } : null);
        } else {
            setCurrentRecord(null);
            setFormData({
                yearMonth: selectedMonth,
                weekNumber: 1,
                subject: subjects.length > 0 ? subjects[0].name : '',
                content: '',
                imagePath: ''
            });
            setUploadedFile(null);
        }
        setShowModal(true);
    };

    const closeModal = () => {
        setShowModal(false);
        setCurrentRecord(null);
        setUploadedFile(null);
    };

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setFormData(prev => ({ ...prev, [name]: value }));
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            if (currentRecord) {
                await educationAPI.update(currentRecord.id, formData);
            } else {
                await educationAPI.create(formData);
            }
            fetchRecords();
            closeModal();
        } catch (error) {
            console.error('Error saving education record:', error);
            alert('     .');
        }
    };

    const handleDelete = async (id) => {
        if (!window.confirm(' ?')) return;
        try {
            await educationAPI.delete(id);
            fetchRecords();
        } catch (error) {
            console.error('Error deleting education record:', error);
        }
    };

    const groupedRecords = records.reduce((acc, record) => {
        const week = record.weekNumber;
        if (!acc[week]) acc[week] = [];
        acc[week].push(record);
        return acc;
    }, {});

    if (loading) return <div className="loading">...</div>;

    return (
        <div className="container">
            <div className="page-header">
                <h1> </h1>
                <p>   </p>
            </div>

            <div className="card">
                <div className="filter-section">
                    <div className="filter-group">
                        <label> :</label>
                        <input
                            type="month"
                            className="form-control"
                            value={selectedMonth}
                            onChange={(e) => setSelectedMonth(e.target.value)}
                        />
                    </div>
                    <div className="actions-group">
                        <button className="btn btn-secondary" onClick={() => setShowSubjectModal(true)}>
                            <FiSettings />  
                        </button>
                        <button className="btn btn-primary" onClick={() => openModal()}>
                            <FiPlus />   
                        </button>
                    </div>
                </div>

                <div className="summary-cards">
                    <div className="summary-card small">
                        <h4> </h4>
                        <p className="count">{records.length}</p>
                    </div>
                    <div className="summary-card small">
                        <h4></h4>
                        <p className="count">{Object.keys(groupedRecords).length}</p>
                    </div>
                    <div className="summary-card small">
                        <h4> </h4>
                        <p className="count">{subjects.length}</p>
                    </div>
                </div>

                {Object.keys(groupedRecords).sort((a, b) => a - b).map(week => (
                    <div key={week} style={{ marginBottom: '30px' }}>
                        <h3 style={{ marginBottom: '16px', padding: '12px', background: '#f5f5f5', borderRadius: '8px' }}>
                            {week}
                        </h3>
                        <div className="table-container">
                            <table>
                                <thead>
                                    <tr>
                                        <th></th>
                                        <th></th>
                                        <th> </th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {groupedRecords[week].map(record => (
                                        <tr key={record.id}>
                                            <td><span className="badge category-badge">{record.subject}</span></td>
                                            <td style={{ whiteSpace: 'pre-line', maxWidth: '400px' }}>{record.content || '-'}</td>
                                            <td>
                                                {record.imagePath ? (
                                                    <a href={`${process.env.REACT_APP_API_URL}${record.imagePath}`} target="_blank" rel="noopener noreferrer">
                                                        <FiImage />  
                                                    </a>
                                                ) : '-'}
                                            </td>
                                            <td>
                                                <div className="actions">
                                                    <button className="action-btn edit-btn" onClick={() => openModal(record)}>
                                                        <FiEdit /> 
                                                    </button>
                                                    <button className="action-btn delete-btn" onClick={() => handleDelete(record.id)}>
                                                        <FiTrash2 /> 
                                                    </button>
                                                </div>
                                            </td>
                                        </tr>
                                    ))}
                                </tbody>
                            </table>
                        </div>
                    </div>
                ))}

                {records.length === 0 && (
                    <div className="empty-state" style={{ padding: '60px', textAlign: 'center', color: '#999' }}>
                          . "  "    .
                    </div>
                )}
            </div>

            {/*   /  */}
            {showModal && (
                <div className="modal-overlay" onClick={closeModal}>
                    <div className="modal-content" onClick={(e) => e.stopPropagation()} style={{ maxWidth: '800px' }}>
                        <div className="modal-header">
                            <h2>{currentRecord ? '  ' : '  '}</h2>
                            <button className="close-btn" onClick={closeModal}><FiX /></button>
                        </div>
                        <form onSubmit={handleSubmit}>
                            <div className="modal-body">
                                <div className="form-row">
                                    <div className="form-group">
                                        <label> *</label>
                                        <input
                                            type="month"
                                            name="yearMonth"
                                            className="form-control"
                                            value={formData.yearMonth}
                                            onChange={handleInputChange}
                                            required
                                        />
                                    </div>
                                    <div className="form-group">
                                        <label> *</label>
                                        <select
                                            name="weekNumber"
                                            className="form-control"
                                            value={formData.weekNumber}
                                            onChange={handleInputChange}
                                            required
                                        >
                                            {[1, 2, 3, 4, 5].map(w => (
                                                <option key={w} value={w}>{w}</option>
                                            ))}
                                        </select>
                                    </div>
                                </div>
                                <div className="form-group">
                                    <label> *</label>
                                    <select
                                        name="subject"
                                        className="form-control"
                                        value={formData.subject}
                                        onChange={handleInputChange}
                                        required
                                    >
                                        <option value=""> </option>
                                        {subjects.map(subject => (
                                            <option key={subject.id} value={subject.name}>{subject.name}</option>
                                        ))}
                                    </select>
                                </div>
                                <div className="form-group">
                                    <label>  *</label>
                                    <textarea
                                        name="content"
                                        className="form-control"
                                        rows="6"
                                        value={formData.content}
                                        onChange={handleInputChange}
                                        required
                                        placeholder="  &#10;:&#10;-   &#10;-   &#10;-   "
                                    ></textarea>
                                </div>
                                <div className="form-group">
                                    <label> </label>
                                    <div style={{ display: 'flex', gap: '10px', alignItems: 'center' }}>
                                        <input
                                            type="file"
                                            accept="image/*"
                                            onChange={handleFileUpload}
                                            style={{ display: 'none' }}
                                            id="image-upload"
                                        />
                                        <label htmlFor="image-upload" className="btn btn-secondary" style={{ margin: 0, cursor: 'pointer' }}>
                                            <FiUpload /> {uploading ? ' ...' : ' '}
                                        </label>
                                        {uploadedFile && (
                                            <span style={{ color: '#4a90e2' }}>
                                                <FiImage /> {uploadedFile.originalFilename || ' '}
                                            </span>
                                        )}
                                    </div>
                                    {uploadedFile && (
                                        <img
                                            src={`${process.env.REACT_APP_API_URL}${uploadedFile.path}`}
                                            alt="Preview"
                                            style={{ marginTop: '10px', maxWidth: '100%', maxHeight: '200px', borderRadius: '8px' }}
                                        />
                                    )}
                                </div>
                            </div>
                            <div className="modal-footer">
                                <button type="button" className="btn btn-secondary" onClick={closeModal}></button>
                                <button type="submit" className="btn btn-primary">{currentRecord ? '' : ''}</button>
                            </div>
                        </form>
                    </div>
                </div>
            )}

            {/*    */}
            {showSubjectModal && (
                <div className="modal-overlay" onClick={() => setShowSubjectModal(false)}>
                    <div className="modal-content" onClick={(e) => e.stopPropagation()}>
                        <div className="modal-header">
                            <h2> </h2>
                            <button className="close-btn" onClick={() => setShowSubjectModal(false)}><FiX /></button>
                        </div>
                        <div className="modal-body">
                            <div className="form-group">
                                <label>  </label>
                                <div style={{ display: 'flex', gap: '10px' }}>
                                    <input
                                        type="text"
                                        className="form-control"
                                        placeholder=""
                                        value={newSubject.name}
                                        onChange={(e) => setNewSubject(prev => ({ ...prev, name: e.target.value }))}
                                    />
                                    <button className="btn btn-primary" onClick={handleAddSubject}>
                                        <FiPlus /> 
                                    </button>
                                </div>
                            </div>
                            <div className="table-container">
                                <table>
                                    <thead>
                                        <tr>
                                            <th></th>
                                            <th></th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {subjects.map(subject => (
                                            <tr key={subject.id}>
                                                <td>{subject.name}</td>
                                                <td>
                                                    <button className="action-btn delete-btn" onClick={() => handleDeleteSubject(subject.id)}>
                                                        <FiTrash2 /> 
                                                    </button>
                                                </td>
                                            </tr>
                                        ))}
                                    </tbody>
                                </table>
                            </div>
                        </div>
                        <div className="modal-footer">
                            <button type="button" className="btn btn-secondary" onClick={() => setShowSubjectModal(false)}></button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
}

export default Education;
