import React, { useState, useEffect } from 'react';
import { scheduleAPI } from '../services/api';
import { FiPlus, FiEdit, FiTrash2, FiX, FiCalendar } from 'react-icons/fi';
import Calendar from 'react-calendar';
import 'react-calendar/dist/Calendar.css';
import './Common.css';
import './Attendance.css';

function Schedule() {
    const [schedules, setSchedules] = useState([]);
    const [loading, setLoading] = useState(true);
    const [showModal, setShowModal] = useState(false);
    const [currentSchedule, setCurrentSchedule] = useState(null);
    const [selectedDate, setSelectedDate] = useState(new Date());
    const [formData, setFormData] = useState({
        scheduleDate: new Date().toISOString().split('T')[0],
        title: '',
        description: ''
    });

    useEffect(() => {
        fetchSchedules();
    }, []);

    const fetchSchedules = async () => {
        try {
            const response = await scheduleAPI.getAll();
            setSchedules(response.data);
        } catch (error) {
            console.error('Error fetching schedules:', error);
        } finally {
            setLoading(false);
        }
    };

    const openModal = (schedule = null) => {
        if (schedule) {
            setCurrentSchedule(schedule);
            setFormData({
                scheduleDate: schedule.scheduleDate,
                title: schedule.title,
                description: schedule.description || ''
            });
        } else {
            setCurrentSchedule(null);
            setFormData({
                scheduleDate: selectedDate.toISOString().split('T')[0],
                title: '',
                description: ''
            });
        }
        setShowModal(true);
    };

    const closeModal = () => {
        setShowModal(false);
        setCurrentSchedule(null);
    };

    const handleInputChange = (e) => {
        const { name, value } = e.target;
        setFormData(prev => ({ ...prev, [name]: value }));
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            if (currentSchedule) {
                await scheduleAPI.update(currentSchedule.id, formData);
            } else {
                await scheduleAPI.create(formData);
            }
            fetchSchedules();
            closeModal();
        } catch (error) {
            console.error('Error saving schedule:', error);
            alert('    .');
        }
    };

    const handleDelete = async (id) => {
        if (!window.confirm(' ?')) return;
        try {
            await scheduleAPI.delete(id);
            fetchSchedules();
        } catch (error) {
            console.error('Error deleting schedule:', error);
        }
    };

    const getSchedulesForDate = (date) => {
        const dateStr = date.toISOString().split('T')[0];
        return schedules.filter(s => s.scheduleDate === dateStr);
    };

    const tileContent = ({ date, view }) => {
        if (view === 'month') {
            const daySchedules = getSchedulesForDate(date);
            if (daySchedules.length > 0) {
                return (
                    <div className="schedule-marker">
                        {daySchedules.length}
                    </div>
                );
            }
        }
        return null;
    };

    if (loading) return <div className="loading">...</div>;

    return (
        <div className="container">
            <div className="page-header">
                <h1> </h1>
                <p>  </p>
            </div>

            <div className="card">
                <div className="filter-section">
                    <button className="btn btn-primary" onClick={() => openModal()}>
                        <FiPlus />  
                    </button>
                </div>

                <div style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '30px', alignItems: 'start' }}>
                    <div className="calendar-view">
                        <Calendar
                            onChange={(date) => setSelectedDate(date)}
                            value={selectedDate}
                            tileContent={tileContent}
                            locale="ko-KR"
                        />
                    </div>

                    <div>
                        <h3 style={{ marginBottom: '16px' }}>
                            {selectedDate.toLocaleDateString('ko-KR')} 
                        </h3>
                        <div className="table-container">
                            <table>
                                <thead>
                                    <tr>
                                        <th></th>
                                        <th></th>
                                        <th></th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {getSchedulesForDate(selectedDate).length === 0 ? (
                                        <tr>
                                            <td colSpan="3" className="empty-state"> </td>
                                        </tr>
                                    ) : (
                                        getSchedulesForDate(selectedDate).map(schedule => (
                                            <tr key={schedule.id}>
                                                <td><strong>{schedule.title}</strong></td>
                                                <td>{schedule.description || '-'}</td>
                                                <td>
                                                    <div className="actions">
                                                        <button className="action-btn edit-btn" onClick={() => openModal(schedule)}>
                                                            <FiEdit />
                                                        </button>
                                                        <button className="action-btn delete-btn" onClick={() => handleDelete(schedule.id)}>
                                                            <FiTrash2 />
                                                        </button>
                                                    </div>
                                                </td>
                                            </tr>
                                        ))
                                    )}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>

                <div style={{ marginTop: '30px' }}>
                    <h3 style={{ marginBottom: '16px' }}> </h3>
                    <div className="table-container">
                        <table>
                            <thead>
                                <tr>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                    <th></th>
                                </tr>
                            </thead>
                            <tbody>
                                {schedules.length === 0 ? (
                                    <tr>
                                        <td colSpan="4" className="empty-state"> </td>
                                    </tr>
                                ) : (
                                    schedules.map(schedule => (
                                        <tr key={schedule.id}>
                                            <td>{new Date(schedule.scheduleDate).toLocaleDateString('ko-KR')}</td>
                                            <td><strong>{schedule.title}</strong></td>
                                            <td>{schedule.description || '-'}</td>
                                            <td>
                                                <div className="actions">
                                                    <button className="action-btn edit-btn" onClick={() => openModal(schedule)}>
                                                        <FiEdit /> 
                                                    </button>
                                                    <button className="action-btn delete-btn" onClick={() => handleDelete(schedule.id)}>
                                                        <FiTrash2 /> 
                                                    </button>
                                                </div>
                                            </td>
                                        </tr>
                                    ))
                                )}
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>

            {/*  /  */}
            {showModal && (
                <div className="modal-overlay" onClick={closeModal}>
                    <div className="modal-content" onClick={(e) => e.stopPropagation()}>
                        <div className="modal-header">
                            <h2>{currentSchedule ? ' ' : ' '}</h2>
                            <button className="close-btn" onClick={closeModal}><FiX /></button>
                        </div>
                        <form onSubmit={handleSubmit}>
                            <div className="modal-body">
                                <div className="form-group">
                                    <label> *</label>
                                    <input
                                        type="date"
                                        name="scheduleDate"
                                        className="form-control"
                                        value={formData.scheduleDate}
                                        onChange={handleInputChange}
                                        required
                                    />
                                </div>
                                <div className="form-group">
                                    <label> *</label>
                                    <input
                                        type="text"
                                        name="title"
                                        className="form-control"
                                        value={formData.title}
                                        onChange={handleInputChange}
                                        required
                                        placeholder=":  "
                                    />
                                </div>
                                <div className="form-group">
                                    <label></label>
                                    <textarea
                                        name="description"
                                        className="form-control"
                                        rows="4"
                                        value={formData.description}
                                        onChange={handleInputChange}
                                        placeholder="   "
                                    ></textarea>
                                </div>
                            </div>
                            <div className="modal-footer">
                                <button type="button" className="btn btn-secondary" onClick={closeModal}></button>
                                <button type="submit" className="btn btn-primary">{currentSchedule ? '' : ''}</button>
                            </div>
                        </form>
                    </div>
                </div>
            )}
        </div>
    );
}

export default Schedule;
