import React from 'react';
import { BrowserRouter as Router, Routes, Route, Link, useNavigate } from 'react-router-dom';
import { FiHome, FiUsers, FiCheckSquare, FiDollarSign, FiTrendingDown, FiPieChart, FiCalendar, FiBookOpen, FiLogOut } from 'react-icons/fi';
import PrivateRoute from './components/PrivateRoute';
import Login from './pages/Login';
import Dashboard from './pages/Dashboard';
import Students from './pages/Students';
import Attendance from './pages/Attendance';
import Payments from './pages/Payments';
import Expenses from './pages/Expenses';
import Finance from './pages/Finance';
import Schedule from './pages/Schedule';
import Education from './pages/Education';
import authService from './services/authService';
import './App.css';

function Sidebar() {
  const navigate = useNavigate();
  const user = authService.getUser();

  const handleLogout = () => {
    authService.logout();
    navigate('/login');
  };

  return (
    <nav className="sidebar">
      <div className="sidebar-header">
        <h2>  </h2>
        {user && <p className="user-name">{user.name}</p>}
      </div>
      <ul className="nav-menu">
        <li>
          <Link to="/">
            <FiHome /> <span></span>
          </Link>
        </li>
        <li>
          <Link to="/students">
            <FiUsers /> <span> </span>
          </Link>
        </li>
        <li>
          <Link to="/attendance">
            <FiCheckSquare /> <span> </span>
          </Link>
        </li>
        <li>
          <Link to="/payments">
            <FiDollarSign /> <span> </span>
          </Link>
        </li>
        <li>
          <Link to="/expenses">
            <FiTrendingDown /> <span> </span>
          </Link>
        </li>
        <li>
          <Link to="/finance">
            <FiPieChart /> <span> </span>
          </Link>
        </li>
        <li>
          <Link to="/schedule">
            <FiCalendar /> <span></span>
          </Link>
        </li>
        <li>
          <Link to="/education">
            <FiBookOpen /> <span> </span>
          </Link>
        </li>
      </ul>
      <div className="sidebar-footer">
        <button onClick={handleLogout} className="logout-button">
          <FiLogOut /> <span></span>
        </button>
      </div>
    </nav>
  );
}

function App() {
  return (
    <Router>
      <Routes>
        <Route path="/login" element={<Login />} />
        <Route
          path="/*"
          element={
            <PrivateRoute>
              <div className="app">
                <Sidebar />
                <main className="main-content">
                  <Routes>
                    <Route path="/" element={<Dashboard />} />
                    <Route path="/students" element={<Students />} />
                    <Route path="/attendance" element={<Attendance />} />
                    <Route path="/payments" element={<Payments />} />
                    <Route path="/expenses" element={<Expenses />} />
                    <Route path="/finance" element={<Finance />} />
                    <Route path="/schedule" element={<Schedule />} />
                    <Route path="/education" element={<Education />} />
                  </Routes>
                </main>
              </div>
            </PrivateRoute>
          }
        />
      </Routes>
    </Router>
  );
}

export default App;
